* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*::after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 100%;
  font-size: 14px;
  line-height: 1;
  text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: bold;
}

body {
  font-family: Montserrat;
  font-weight: 400;
}

.section-container {
  width: 100%;
  margin: 0 auto;
  max-width: 1280px;
}

.header {
  padding: 0px 0px;
  margin: 0px 0px;
  position: relative;
  z-index: 50;
}

.header:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.header__background {
  background: url(../img/fon1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.header__rgda {
  background: rgba(31, 32, 33, 0.79);
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__burger {
  display: none;
}

.header__logo {
  z-index: 3;
  margin: 40px 110px 0px 40px;
}

.header__nav {
  z-index: 3;
  margin-right: 300px;
}

.header__nav-list {
  display: flex;
  list-style: none;
}

.header__nav-item {
  position: relative;
  margin: 56px 20px 0px 40px;
}

.header__nav-link {
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
  text-decoration: none;
  padding: 8px;
  white-space: nowrap;
  transition: all 0.3s ease 0s;
}

.header__nav-link:hover {
  border-bottom: 1px solid white;
}

.header__number-container {
  z-index: 3;
}

.header__number {
