@media (max-width: 992px) {
  .section-container {
    max-width: 750px;
  }

  .header__nav-item {
    margin: 60px 0px 5px 116px;
  }

  .header {
    width: 100%;
    position: fixed;
  }

  .header__nav {
    position: fixed;
    overflow: auto;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    z-index: 1;
    padding-top: 60px;
    transition: all 0.3s ease 0s;
  }

  .header__nav.active {
    top: 0;
  }

  html.lock,
  body.lock {
    overflow: hidden;
  }

  .header__nav-link {
    padding: 0px;
  }

  .header__nav-link:hover {
    border-top: none;
  }

  .header__nav-list {
    display: block;
  }

  .header__burger {
    order: 3;
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 3;
  }

  .header__burger:hover {
    cursor: pointer;
  }

  .header__burger:before,
  .header__burger:after {
    content: '';
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  .header__burger:before {
    top: 0;
  }

  .header__burger:after {
    bottom: 0;
  }

  .header__burger.active:before {
    content: '';
    transform: rotate(45deg);
    top: 9px;
  }

  .header__burger.active:after {
    content: '';
    transform: rotate(-45deg);
    bottom: 9px;
  }

  .header__burger.active span {
    transform: scale(0);
  }

  .header__burger span {
    position: absolute;
    background-color: #fff;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;
  }

